import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import page from '@/components/common-components/page.vue';
import pageView from '@/components/common-ui/page-view.vue';
import addNotice from './addForm.vue';
import { deepClone } from '@/until/index';
import rightBox from '@/components/common-components/right-box.vue';
let buildingConfig = class buildingConfig extends Vue {
    constructor() {
        super(...arguments);
        this.className = '楼栋配置';
        this.showBtn = false;
        this.oprateConfig = [{
                text: '添加',
                action: 'add'
            }];
        this.noticeList = deepClone(this.buildingConfig);
    }
    showPopup(refId) {
        this.maskClick();
        const component = this.$refs[refId];
        component.show();
    }
    hidePopup(refId) {
        const component = this.$refs[refId];
        component.hide();
    }
    get buildingConfig() {
        return this.$store.state.schoolInfo.buildingConfig || [];
    }
    get schoolInfo() {
        return this.$store.state.schoolInfo;
    }
    maskClick() {
        this.$refs.addNotice.reset();
    }
    submit(model) {
        this.noticeList.push({
            title: model.title
        });
        this.showBtn = true;
        this.hidePopup('myPopup');
    }
    deleteNotice(index) {
        this.noticeList.splice(index, 1);
        this.showBtn = true;
    }
    saveSchoolInfo() {
        this.$store.dispatch('updateSchoolInfo', {
            schoolId: this.schoolInfo.id,
            type: 'buildingConfig',
            value: this.noticeList
        }).then((e) => {
            if (e.success) {
                this.showBtn = false;
                this.showToastType();
            }
            this.$store.commit('SET_SCHOOL_INFO_BY_TYPE', {
                type: 'buildingConfig',
                value: this.noticeList
            });
        });
    }
    showToastType() {
        const toast = this.$createToast({
            txt: '保存成功',
            type: 'correct'
        });
        toast.show();
    }
    boxClick(action) {
        if (action === 'add') {
            this.showPopup('myPopup');
        }
    }
};
buildingConfig = __decorate([
    Component({
        components: {
            page,
            pageView,
            addNotice,
            rightBox
        }
    })
], buildingConfig);
export default buildingConfig;
